.article {
    margin-bottom: 10rem; // 100px
}

.article__header {
    margin-bottom: 2rem; // 50px
    @media screen and (min-width: $bp__sm) {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        -webkit-box-pack: justify;
        -webkit-justify-content: space-between;
        -ms-flex-pack: justify;
        justify-content: space-between;
        -webkit-box-align: baseline;
        -webkit-align-items: baseline;
        -ms-flex-align: baseline;
        align-items: baseline;
    }
}

.article__title {
    @include fs--heading-1;
    color: $c-base__02;
}

.article__time {
    @include fs--body;
    color: $c-base__01;
}

.article__main {
    margin-bottom: 5rem; // 50px
    @include fs--body;
    color: $c-base__01;

    > * {
        margin-bottom: 1.8rem; // 18px
    }

    h2 {
        @include fs--heading-2;
    }

    h3 {
        @include fs--heading-3;
    }

    h4 {
        @include fs--heading-4;
    }

    h5 {
        @include fs--body;
    }

    strong {
        color: $c-base__02;
        font-weight: 700;
    }

    blockquote {
        margin-left: 0;
        margin-right: 0;
        padding-left: 1.8rem; // 18px
        border-left: 5px solid #ccc;
    }

    ul,
    ol {
        margin-left: 2.1rem;
    }

    .web-container {
        position: relative;
        width: 100%;
        max-width: 100%;
        height: 0;
        padding-bottom: 62.5%;
        border: 1px solid #cacaca;
        overflow: hidden;
    }

    .web-container iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        max-width: 100%;
        height: 100%;
    }
}

.pagenav {
    width: 100%;
    text-align: center;
    border: 1px solid LightGrey;
    border-left-color: transparent;
    border-right-color: transparent;
    font-size: 18px;
    overflow: hidden;
    margin-bottom: 8rem;
}

.pagenav div {
    border-color: transparent;
}

.wrapper {
    padding: 10px;
    border: 1px solid LightGrey;
    display: inline-block;
    margin: 0 auto;
}

#left {
    float: left;
    text-align: left;
}

#right {
    float: right;
    text-align: right;
}

.formula {
    overflow-x: auto;
}

table {
    width: 100%;
    border-collapse: collapse;
}

th {
    text-align: left;
}

td,th {
    border: 1px solid rgb(220, 220, 220);
    padding: 2px 5px;
}